.templateList {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999999999999999999999999;
  background: #fff;
  background: rgba(255, 255, 255, 0.9);
  overflow-y: scroll;
  transition: all 0.5s ease-in-out;
  transform: scale(0);
}

.templateList__body {
  padding: 10vh;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.no-csstransforms .templateList {
  display: none;
}

.templateList.-isOpen {
  transform: scale(1);
}

.templateList.-isOpen .no-csstransforms {
  display: block;
}

.templateList__focus {
  height: 0;
  display: block;
}

.templateList__close {
  outline: none;
  box-shadow: none;
}

.templateList__heading {
  margin-bottom: 20px;
}

.templateList__list {
  width: 100%;
  max-width: 350px;
  text-align: center;
}

.templateList__item {
  margin-top: 20px;
}

.templateList__item:first-child {
  margin-top: 0;
}

.templateList__link {
  display: block;
  padding: 15px;
  text-transform: capitalize;
}

.templateList__link.-isActive {
  background: #F16031;
}
